/* eslint-disable max-params */
/* eslint-disable max-statements */
/* eslint-disable max-lines */
/* eslint-disable complexity */

import React, { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { PropTypes } from 'react-desc';
import DSTextBox from '../../TextBox';
import setCaretPosition from '../utils/setCaretPosition';

// http://stackoverflow.com/a/10899795/604296
const addThousandsSeparator = (n, thousandsSeparatorSymbol) =>
  n.replace(/\B(?=(\d{3})+(?!\d))/g, thousandsSeparatorSymbol);

const conformValue = (
  prevRawValue,
  prevCursorPos,
  lastkeycode,
  {
    prefix,
    suffix,
    includeThousandsSeparator,
    thousandsSeparatorSymbol,
    allowDecimal,
    decimalSymbol,
    decimalLimit,
    requireDecimal,
    allowNegative,
    allowLeadingZeroes,
    integerLimit,
    shouldCompleteDecimals,
  },
) => {
  const prefixIndex = prevRawValue.indexOf(prefix);
  const suffixIndex = prevRawValue.lastIndexOf(suffix);
  const negativeIndex = prevRawValue.indexOf('-');

  const cursorPos =
    prevCursorPos - (prefixIndex === -1 ? 0 : prefixIndex + prefix.length);

  const rawValue = prevRawValue.substring(
    prefixIndex === -1 ? 0 : prefixIndex + prefix.length,
    suffixIndex === -1 ? prevRawValue.length : suffixIndex,
  );

  const filteredRawValue = rawValue
    .split('')
    .filter(
      (char) =>
        (char >= '0' && char <= '9') || char === decimalSymbol || char === '-',
    );

  let isNegative =
    allowNegative &&
    negativeIndex !== -1 &&
    (prefixIndex === -1 || negativeIndex < prefixIndex);

  let integer = '';
  let decimal = '';
  let foundDecimalSymbol = false;

  const integerFits = () =>
    integerLimit === null || integer.length < integerLimit;
  const decimalFits = () =>
    decimalLimit === null || decimal.length < decimalLimit;

  filteredRawValue.forEach((char) => {
    if (allowDecimal && !foundDecimalSymbol && char === decimalSymbol) {
      foundDecimalSymbol = true;
    } else if (
      allowNegative &&
      !integer.length &&
      !foundDecimalSymbol &&
      char === '-'
    ) {
      isNegative = true;
    } else if (char >= '0' && char <= '9') {
      if (!foundDecimalSymbol && integerFits()) {
        integer += char;
      } else if (foundDecimalSymbol && decimalFits()) {
        decimal += char;
      }
    }
  });

  if (requireDecimal) foundDecimalSymbol = true;
  if (!requireDecimal && !decimal?.length && lastkeycode === 8) {
    foundDecimalSymbol = false;
  }
  if (!integer && !decimal) foundDecimalSymbol = false;

  if (foundDecimalSymbol && !integer && decimal) {
    integer = '0';
  }

  if (!allowLeadingZeroes) {
    while (integer.length > 1 && integer[0] === '0')
      integer = integer.substring(1);
  }

  let nextMaskedValue = includeThousandsSeparator
    ? addThousandsSeparator(integer, thousandsSeparatorSymbol)
    : integer;
  nextMaskedValue += foundDecimalSymbol ? decimalSymbol : '';

  if (shouldCompleteDecimals)
    while (decimal.length !== decimalLimit) decimal += '0';

  nextMaskedValue += decimal;

  let maskedPos = 0;
  for (let i = 0; i < cursorPos; i += 1) {
    if (nextMaskedValue[maskedPos] === thousandsSeparatorSymbol) maskedPos += 1;
    if (nextMaskedValue[maskedPos] === rawValue[i]) maskedPos += 1;
  }
  if (nextMaskedValue.length || isNegative) {
    nextMaskedValue = prefix + nextMaskedValue;
    maskedPos += prefix.length;

    if (isNegative) {
      if (lastkeycode !== 8 || nextMaskedValue !== prefix)
        nextMaskedValue = `-${nextMaskedValue}`;
      maskedPos += 1;
    }

    nextMaskedValue += suffix;
  }

  if (lastkeycode !== 8 && maskedPos < nextMaskedValue.length) {
    if (
      nextMaskedValue[maskedPos] === decimalSymbol &&
      integer.length === integerLimit
    ) {
      maskedPos += 1;
    } else if (nextMaskedValue[maskedPos] === thousandsSeparatorSymbol) {
      maskedPos += 1;
    }
  }

  return {
    nextMaskedValue,
    maskedPos: Math.min(maskedPos, nextMaskedValue.length - suffix.length),
  };
};

const NumberInputMask = ({
  focus,
  setFocus,
  setRawMask,
  cursorPosition,
  setCursorPosition,
  lastkey,
  setLastkey,
  setRawValue,
  allowDecimal,
  allowLeadingZeroes,
  allowNegative,
  autoFocus,
  className,
  clearable = false,
  decimalLimit,
  decimalSymbol,
  disabled,
  fluidWidth,
  hasError,
  includeThousandsSeparator,
  innerRef,
  integerLimit,
  leftComponent,
  maxLength,
  minLength,
  name,
  onBlur,
  onChange,
  onClick,
  onFocus,
  onKeyDown,
  onKeyUp,
  onPaste,
  placeholder,
  prefix,
  readOnly,
  requireDecimal,
  rightComponent,
  style,
  suffix,
  thousandsSeparatorSymbol,
  type,
  value,
  ...rest
}) => {
  const inputRef = useRef();
  const [maskedValue, setMaskedValue] = useState(value);

  const conform = useCallback(
    ({
      rawValue,
      cursorPos,
      lastkeycode,
      shouldUpdateCursorPos = true,
      shouldCompleteDecimals = false,
    }) => {
      const { nextMaskedValue, maskedPos } = conformValue(
        rawValue,
        cursorPos,
        lastkeycode,
        {
          prefix,
          suffix,
          includeThousandsSeparator,
          thousandsSeparatorSymbol,
          allowDecimal,
          decimalSymbol,
          decimalLimit,
          requireDecimal,
          allowNegative,
          allowLeadingZeroes,
          integerLimit,
          shouldCompleteDecimals,
        },
      );
      const event = {
        target: {
          value: nextMaskedValue,
          originalValue: rawValue,
        },
      };
      if (onChange && nextMaskedValue !== maskedValue) onChange(event);

      setMaskedValue(nextMaskedValue);
      setRawValue(nextMaskedValue);

      if (shouldUpdateCursorPos) setCursorPosition({ current: maskedPos });
    },
    [maskedValue, onChange],
  );

  const handleOnChange = useCallback(
    (e) => {
      const { value: rawValue, selectionEnd } = e.target;
      setRawMask(rawValue);
      setRawValue(rawValue);
      setCursorPosition({ current: selectionEnd });
      setFocus(true);
    },
    [setRawMask, setRawValue, setCursorPosition, setFocus, lastkey.code],
  );

  useLayoutEffect(() => {
    if (focus) setCaretPosition(inputRef.current, cursorPosition.current);
  }, [focus, cursorPosition]);

  useLayoutEffect(() => {
    // if the value changes, then re-conform the mask
    conform({
      rawValue: value,
      cursorPos: cursorPosition.current,
      lastkeycode: lastkey.code,
      shouldUpdateCursorPos: focus,
    });
  }, [focus, value, cursorPosition.current]);

  const handleKeyDown = useCallback(
    (e) => {
      e.stopPropagation();
      setLastkey({ key: e.key, code: e.keyCode });
      onKeyDown(e);
    },
    [onKeyDown],
  );

  const handleBlur = useCallback(
    (e) => {
      setFocus(false);
      onBlur(e);
      if (requireDecimal && maskedValue !== '') {
        conform({
          rawValue: maskedValue,
          cursorPos: 0,
          lastkey: 0,
          shouldUpdateCursorPos: 0,
          shouldCompleteDecimals: true,
        });
      }
    },
    [maskedValue, requireDecimal, suffix, decimalLimit, onBlur],
  );

  return (
    <DSTextBox
      {...rest}
      className={className}
      clearable={clearable}
      disabled={disabled}
      fluidWidth={fluidWidth}
      hasError={hasError}
      innerRef={(ref) => {
        inputRef.current = ref;
        if (innerRef) innerRef(ref);
      }}
      isActive={focus}
      leftComponent={leftComponent}
      maxLength={maxLength}
      minLength={minLength}
      name={name}
      onBlur={handleBlur}
      onChange={handleOnChange}
      onClick={onClick}
      onFocus={onFocus}
      onKeyDown={handleKeyDown}
      onKeyUp={onKeyUp}
      onPaste={onPaste}
      placeholder={placeholder}
      readOnly={readOnly}
      rightComponent={rightComponent}
      style={style}
      type={type}
      value={maskedValue}
    />
  );
};

NumberInputMask.propTypes = {
  focus: PropTypes.bool,
  setFocus: PropTypes.func,
  setRawMask: PropTypes.func,
  cursorPosition: PropTypes.shape({ current: PropTypes.number }),
  setCursorPosition: PropTypes.func,
  lastkey: PropTypes.shape({ code: PropTypes.number, key: PropTypes.string }),
  setLastkey: PropTypes.func,
  setRawValue: PropTypes.func,
  allowDecimal: PropTypes.bool,
  allowLeadingZeroes: PropTypes.bool,
  allowNegative: PropTypes.bool,
  autoFocus: PropTypes.bool,
  className: PropTypes.string,
  clearable: PropTypes.bool,
  decimalLimit: PropTypes.number,
  decimalSymbol: PropTypes.string,
  disabled: PropTypes.bool,
  fluidWidth: PropTypes.bool,
  hasError: PropTypes.bool,
  includeThousandsSeparator: PropTypes.bool,
  innerRef: PropTypes.any,
  integerLimit: PropTypes.number,
  leftComponent: PropTypes.element,
  mask: PropTypes.any,
  maxLength: PropTypes.number,
  minLength: PropTypes.number,
  name: PropTypes.string,
  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  onClick: PropTypes.func,
  onFocus: PropTypes.func,
  onKeyDown: PropTypes.func,
  onKeyUp: PropTypes.func,
  onPaste: PropTypes.func,
  placeholder: PropTypes.string,
  placeholderChar: PropTypes.string,
  prefix: PropTypes.string,
  readOnly: PropTypes.bool,
  requireDecimal: PropTypes.bool,
  rightComponent: PropTypes.element,
  style: PropTypes.object,
  suffix: PropTypes.string,
  thousandsSeparatorSymbol: PropTypes.string,
  type: PropTypes.string,
  value: PropTypes.string,
};

export default NumberInputMask;
