/* eslint-disable max-lines */
import React, { useCallback, useEffect, useState } from 'react';
import { describe } from 'react-desc';
import { MASK_TYPES } from './MaskTypes';
import { MASK_PIPES } from './MaskPipes';
import DSInputMaskDeprecated from './DSInputMaskDeprecated';
import { inputProps } from './props';
import { defaultProps } from './defaultProps';
import { OutOfTheBoxMaskTypes } from './mask_types';

const isStringMask = (mask: any) => typeof mask === 'string';

const isObjectMask = (mask: any) => typeof mask === 'object' && mask.type;

const getRawMask = (mask: any, rawValue: string) =>
  typeof mask === 'function' ? mask(rawValue) : mask;

const getValueFromEventOrString = (e: any): string => {
  let value = e;
  if (e && e.target) {
    const {
      target: { value: eValue },
    } = e;
    value = eValue;
  }
  return value;
};

const DSInputMask = ({
  'aria-label': ariaLabel = 'Input Mask',
  autoFocus,
  innerRef,
  mask,
  pipe,
  value: valueOrEvent,
  ...rest
}) => {
  const [rawValue, setRawValue] = useState(
    getValueFromEventOrString(valueOrEvent),
  );

  useEffect(() => {
    setRawValue(getValueFromEventOrString(valueOrEvent));
  }, [valueOrEvent]);

  const [rawMask, setRawMask] = useState<any>(getRawMask(mask, rawValue));

  const [focus, setFocus] = useState(autoFocus);

  const [lastkey, setLastkey] = useState<{ code: number; key: string }>({
    code: null,
    key: null,
  });

  const [cursorPosition, setCursorPosition] = useState({ current: 0 });

  const handleSetMask = useCallback(
    (newValue) => {
      setRawMask(getRawMask(mask, newValue));
    },
    [mask],
  );

  if (!pipe && isStringMask(rawMask)) {
    return OutOfTheBoxMaskTypes[rawMask]({
      ...rest,
      'aria-label': ariaLabel,
      innerRef,
      value: rawValue,
      focus,
      setFocus,
      setRawMask: handleSetMask,
      cursorPosition,
      setCursorPosition,
      lastkey,
      setLastkey,
      setRawValue,
    });
  }

  if (!pipe && isObjectMask(rawMask)) {
    // we should change rawMask.type to rawMask.maskType
    // to avoid override standard props
    return OutOfTheBoxMaskTypes[rawMask.type]({
      ...rawMask,
      ...rest,
      'aria-label': ariaLabel,
      innerRef,
      value: rawValue,
      focus,
      setFocus,
      setRawMask: handleSetMask,
      cursorPosition,
      setCursorPosition,
      lastkey,
      setLastkey,
      setRawValue,
    });
  }

  const getDeprecatedMask = () => {
    if (mask === MASK_TYPES.DATE) return MASK_TYPES.DATE_DEPRECATED;
    if (mask === MASK_TYPES.DATE_TIME) return MASK_TYPES.DATE_TIME_DEPRECATED;
    return mask;
  };
  return (
    <DSInputMaskDeprecated
      {...rest}
      aria-label={ariaLabel}
      autoFocus={autoFocus}
      innerRef={innerRef}
      mask={getDeprecatedMask()}
      pipe={pipe}
      value={rawValue}
    />
  );
};

DSInputMask.propTypes = inputProps;
DSInputMask.defaultProps = defaultProps;

const DSInputMaskWithSchema = describe(DSInputMask).description(
  'Masking input for custom formats',
);
DSInputMaskWithSchema.propTypes = inputProps;

export { MASK_TYPES, MASK_PIPES, DSInputMaskWithSchema };
export default DSInputMask;
