import React, { useState, useEffect, useRef, useCallback } from 'react';
import { describe } from 'react-desc';
import DSDropdownMenu from '@elliemae/ds-dropdownmenu';
import DSInputMask from '@elliemae/ds-form/InputMask';
import { InputSearchWrapper } from './components/blocks';
import { createDisabledOption, noAddonOptions } from './components/helper';
import { zipcodeSearchProps, defaultProps } from './props';

const DSZipCodeSearch = ({
  disabled,
  value,
  placeholder,
  mask,
  onBlur,
  onChange,
  onSearch,
  containerProps,
  selectRightOptions,
  activeCount,
  options,
  tabIndex,
}) => {
  const ref = useRef(null);
  const [searchValue, setSearchValue] = useState(value);
  const [isOpen, setIsOpen] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const [isInputFocus, setIsInputFocus] = useState(false);
  const [isSelected, setIsSelected] = useState(false);

  useEffect(() => {
    if (isInputFocus && activeCount <= searchValue.length && !isSelected) {
      setIsOpen(true);
      setIsActive(true);
    } else if (!isActive || activeCount > searchValue.length) {
      setIsOpen(false);
    }
  }, [isInputFocus, isActive, activeCount, searchValue]);
  useEffect(() => {
    setSearchValue(value || '');
  }, [value]);

  const onTypeInInput = useCallback(
    (e) => {
      const finalVal = e.target.value;
      onSearch(finalVal);
      setSearchValue(finalVal);
      setIsSelected(false);
    },
    [onSearch],
  );

  const onFocusInput = useCallback(() => {
    setIsInputFocus(true);
  }, []);

  const onInnerBlur = useCallback(
    (e) => {
      setIsInputFocus(false);
      onBlur(e);
    },
    [onBlur],
  );
  const onClose = useCallback(() => {
    setIsActive(false);
  }, []);
  const onSelectItem = useCallback((item) => {
    setSearchValue(item.value);
    onChange(item);
    setIsSelected(true);
    ref.current.focus();
    setIsOpen(false);
    onClose();
  }, []);

  return (
    <InputSearchWrapper {...containerProps}>
      <DSDropdownMenu
        isOpen={isOpen && options.length > 0}
        menuProps={{ className: 'input-search-menu' }}
        // onBlur={() => console.log('inblur;')}
        onClose={onClose}
        onSelectMenuItem={onSelectItem}
        options={
          options.length > 0
            ? [
                createDisabledOption(selectRightOptions),
                ...noAddonOptions(options),
              ]
            : []
        }
        triggerComponent={
          <DSInputMask
            disabled={disabled}
            innerRef={ref}
            mask={mask}
            onBlur={onInnerBlur}
            onChange={onTypeInInput}
            onFocus={onFocusInput}
            placeholder={placeholder}
            value={searchValue}
            tabIndex={tabIndex}
          />
        }
      />
    </InputSearchWrapper>
  );
};

DSZipCodeSearch.propTypes = zipcodeSearchProps;

DSZipCodeSearch.defaultProps = defaultProps;

const ZipCodeSearchWithSchema = describe(DSZipCodeSearch);
ZipCodeSearchWithSchema.propTypes = zipcodeSearchProps;

export { DSZipCodeSearch, ZipCodeSearchWithSchema };
export default DSZipCodeSearch;
