import React from 'react';
import DateInputMask from './DateInputMask';
import DateTimeInputMask from './DateTimeInputMask';
import DictionaryInputMask from './DictionaryInputMask';
import NumberInputMask from './NumberInputMask';
import PhoneInputMask from './PhoneInputMask';
import PhoneInternationalInputMask from './PhoneInternationalInputMask';
import SsnInputMask from './SsnInputMask';
import UsZipCodeInputMask from './UsZipCodeInputMask';
import ZipCodeSearchInputMask from './ZipCodeSearchInputMask';

const dollarSign = '$';
const percentSign = '%';
const emptyString = '';
const comma = ',';
const period = '.';

export const OutOfTheBoxMaskTypes = {
  'ds-mask-date': (props) => <DateInputMask {...props} />,
  'ds-mask-date-time': (props) => <DateTimeInputMask {...props} />,
  'ds-mask-dictionary': ({ ignoreCase = true, ...rest }) => (
    <DictionaryInputMask ignoreCase={ignoreCase} {...rest} />
  ),
  'ds-mask-number': ({
    prefix = dollarSign,
    suffix = emptyString,
    includeThousandsSeparator = true,
    thousandsSeparatorSymbol = comma,
    allowDecimal = false,
    decimalSymbol = period,
    decimalLimit = 2,
    requireDecimal = false,
    allowNegative = false,
    allowLeadingZeroes = false,
    integerLimit = null,
    ...rest
  }) => (
    <NumberInputMask
      prefix={prefix}
      suffix={suffix}
      includeThousandsSeparator={includeThousandsSeparator}
      thousandsSeparatorSymbol={thousandsSeparatorSymbol}
      allowDecimal={allowDecimal}
      decimalSymbol={decimalSymbol}
      decimalLimit={decimalLimit}
      requireDecimal={requireDecimal}
      allowNegative={allowNegative}
      allowLeadingZeroes={allowLeadingZeroes}
      integerLimit={integerLimit}
      {...rest}
    />
  ),
  'ds-mask-percent': ({
    prefix = emptyString,
    suffix = percentSign,
    includeThousandsSeparator = true,
    thousandsSeparatorSymbol = comma,
    allowDecimal = false,
    decimalSymbol = period,
    decimalLimit = 2,
    requireDecimal = false,
    allowNegative = false,
    allowLeadingZeroes = false,
    integerLimit = null,
    ...rest
  }) => (
    <NumberInputMask
      prefix={prefix}
      suffix={suffix}
      includeThousandsSeparator={includeThousandsSeparator}
      thousandsSeparatorSymbol={thousandsSeparatorSymbol}
      allowDecimal={allowDecimal}
      decimalSymbol={decimalSymbol}
      decimalLimit={decimalLimit}
      requireDecimal={requireDecimal}
      allowNegative={allowNegative}
      allowLeadingZeroes={allowLeadingZeroes}
      integerLimit={integerLimit}
      {...rest}
    />
  ),
  'ds-mask-phone': (props) => <PhoneInputMask {...props} />,
  'ds-mask-phone-international': (props) => (
    <PhoneInternationalInputMask {...props} />
  ),
  'ds-mask-ssn': (props) => <SsnInputMask {...props} />,
  'ds-mask-us-zip-code': (props) => <UsZipCodeInputMask {...props} />,
  'ds-mask-zip-code-search': (props) => <ZipCodeSearchInputMask {...props} />,
};
