/* eslint-disable complexity */
import React, { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { PropTypes } from 'react-desc';
import DSTextBox from '../../TextBox';
import setCaretPosition from '../utils/setCaretPosition';

const conformValue = (rawValue: string, cursorPos: number) => {
  const nextMaskedValue = rawValue
    .split('')
    .filter((char) => char >= '0' && char <= '9')
    .slice(0, 5);

  let maskedPos = 0;
  for (let i = 0; i < cursorPos; i += 1) {
    if (nextMaskedValue[maskedPos] === rawValue[i]) maskedPos += 1;
  }
  return { nextMaskedValue: nextMaskedValue.join(''), maskedPos };
};

const UsZipCodeInputMask = ({
  focus,
  setFocus,
  setRawMask,
  cursorPosition,
  setCursorPosition,
  lastkey,
  setLastkey,
  setRawValue,
  style,
  disabled,
  className,
  name,
  maxLength,
  minLength,
  fluidWidth,
  placeholder,
  onKeyDown,
  onClick,
  onChange,
  onFocus,
  onBlur,
  onPaste,
  onKeyUp,
  hasError,
  readOnly,
  type,
  innerRef,
  clearable = false,
  leftComponent,
  rightComponent,
  value,
  ...rest
}) => {
  const inputRef = useRef();
  const [maskedValue, setMaskedValue] = useState(value);

  const conform = useCallback(
    ({ rawValue, cursorPos, lastkeycode, shouldUpdateCursorPos = true }) => {
      const { nextMaskedValue, maskedPos } = conformValue(rawValue, cursorPos);
      const event = {
        target: {
          value: nextMaskedValue,
          originalValue: rawValue,
        },
      };
      if (onChange && nextMaskedValue !== maskedValue) onChange(event);

      setMaskedValue(nextMaskedValue);
      setRawValue(nextMaskedValue);

      if (shouldUpdateCursorPos) setCursorPosition({ current: maskedPos });
    },
    [maskedValue, onChange],
  );

  const handleOnChange = useCallback(
    (e) => {
      const { value: rawValue, selectionEnd } = e.target;
      setRawMask(rawValue);
      setRawValue(rawValue);
      setCursorPosition({ current: selectionEnd });
      setFocus(true);
    },
    [setRawMask, setRawValue, setCursorPosition, setFocus, lastkey.code],
  );

  useLayoutEffect(() => {
    if (focus) setCaretPosition(inputRef.current, cursorPosition.current);
  }, [focus, cursorPosition]);

  useLayoutEffect(() => {
    // if the value changes, then re-conform the mask
    conform({
      rawValue: value,
      cursorPos: cursorPosition.current,
      lastkeycode: lastkey.code,
      shouldUpdateCursorPos: focus,
    });
  }, [focus, value, cursorPosition.current]);

  const handleKeyDown = useCallback(
    (e) => {
      e.stopPropagation();
      setLastkey({ key: e.key, code: e.keyCode });
      onKeyDown(e);
    },
    [onKeyDown],
  );

  const handleBlur = useCallback(
    (e) => {
      setFocus(false);
      onBlur(e);
    },
    [onBlur],
  );

  return (
    <DSTextBox
      {...rest}
      className={className}
      clearable={clearable}
      disabled={disabled}
      fluidWidth={fluidWidth}
      hasError={hasError}
      innerRef={(ref) => {
        inputRef.current = ref;
        if (innerRef) innerRef(ref);
      }}
      isActive={focus}
      leftComponent={leftComponent}
      maxLength={maxLength}
      minLength={minLength}
      name={name}
      onBlur={handleBlur}
      onChange={handleOnChange}
      onClick={onClick}
      onFocus={onFocus}
      onKeyDown={handleKeyDown}
      onKeyUp={onKeyUp}
      onPaste={onPaste}
      placeholder={placeholder}
      readOnly={readOnly}
      rightComponent={rightComponent}
      style={style}
      type={type}
      value={maskedValue}
    />
  );
};

UsZipCodeInputMask.propTypes = {
  focus: PropTypes.bool,
  setFocus: PropTypes.func,
  setRawMask: PropTypes.func,
  cursorPosition: PropTypes.shape({ current: PropTypes.number }),
  setCursorPosition: PropTypes.func,
  lastkey: PropTypes.shape({ code: PropTypes.number, key: PropTypes.string }),
  setLastkey: PropTypes.func,
  setRawValue: PropTypes.func,
  style: PropTypes.object,
  disabled: PropTypes.bool,
  className: PropTypes.string,
  name: PropTypes.string,
  maxLength: PropTypes.number,
  minLength: PropTypes.number,
  fluidWidth: PropTypes.bool,
  placeholder: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
  onKeyDown: PropTypes.func,
  leftComponent: PropTypes.element,
  rightComponent: PropTypes.element,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  onPaste: PropTypes.func,
  onKeyUp: PropTypes.func,
  hasError: PropTypes.bool,
  readOnly: PropTypes.bool,
  onClick: PropTypes.func,
  clearable: PropTypes.bool,
  type: PropTypes.string,
  innerRef: PropTypes.any,
  mask: PropTypes.any,
  placeholderChar: PropTypes.string,
};

export default UsZipCodeInputMask;
